import {List, SimpleListItem} from '@rmwc/list'
import '@rmwc/list/styles';
import { useEffect, useState } from 'react';
import moment from 'moment'
import {Typography} from '@rmwc/typography'

function ListContainer2( {schedule, info, periods2, periods3, day, setMax, setIndex, index, now} ){
    useEffect(() => setIndex(day === 0 || day === 6 ? 0 : day - 1), [])

    return (
        <>
        <List twoLine style={{'margin-top': '8px'}}>
        <Typography use="subtitle2" style={{'margin-left': '16px'}}>
                      Tuesday
                      </Typography>
            {
                
                schedule[2].map((a, b) => {
                    var classStart = moment(periods2[b].split('-')[0],'hhmm');
                    var classEnd = moment(periods2[b].split('-')[1],'hhmm');
                    
                    return (
                        <SimpleListItem
                            graphic={info[a].icon}
                            text={a}
                            secondaryText={periods2[b]}
                            selected={index === day - 1 && now.isBefore(classEnd) && classStart.subtract(10, 'minutes').isBefore(now)}
                        />
                    )
                })
            }

        </List>
        
        <List twoLine style={{'margin-top': '8px'}}>
        <Typography use="subtitle2" style={{'margin-left': '16px'}}>
                      Wednesday
                      </Typography>
        {
            
            schedule[3].map((a, b) => {
                var classStart = moment(periods3[b].split('-')[0],'hhmm');
                var classEnd = moment(periods3[b].split('-')[1],'hhmm');
                
                return (
                    <SimpleListItem
                        graphic={info[a].icon}
                        text={a}
                        secondaryText={periods3[b]}
                        selected={index === day - 1 && now.isBefore(classEnd) && classStart.subtract(10, 'minutes').isBefore(now)}
                    />
                )
            })
        }

    </List>
    </>
    )
}

export default ListContainer2